/* Footer styles start */

.footer {
    background: #091242;
    padding: 46px;
}

.footer__menu {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: space-between;
    padding-top: 46px;
    padding-bottom: 46px
}

.footer__menu-item{
    margin-right: 30px;
}

.footer__menu-link {
    color: #FFF;
    font-family: Rubik;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    text-decoration: none;
}

.footer__menu-item:last-child{
    margin-right: 0;
}

.footer__menu-link:hover, .footer__menu-link:focus, .footer__menu-link:active {
    opacity: .75;
}

/* Footer styles end */
